@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wdth,wght@0,87.5,300..800;1,87.5,300..800&display=swap');
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  height: 100vh;
  width: 100%;
}

header{
  width: 100%;
  height: 80px;
  align-content: center;
  background: rgb(255, 0, 85);
}
header nav{
  width: 100%;
  display: flex;
  justify-content: space-between;
}
#check{
  display: none;
}
.checkbtn{
  font-size: 30px;
  color: white;
  float: right;
  line-height: 80px;
  margin-right: 40px;
  cursor: pointer;
  display: none;
}
.logo{
  text-decoration: none;
  color: rgb(0,0,0);
  text-transform: capitalize;
  font-size: 2em;
  font-weight: 700;
}
header nav ul {
  width: 400px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-right: 10px;
  padding: 10px;
}
header nav ul li{
  list-style: none;
}
header nav ul li a{
  text-decoration: none;
  color: rgb(0, 0, 0);
  font-size: 20px;
  cursor: pointer;
  padding: 5px;
  width: 100%;
  text-transform: capitalize;
  font-weight: 500;
}
header nav ul li a:hover{
transition: 1s;
background-color: rgba(0, 0, 0, .3);
border-bottom: 3px solid pink;
border-radius: 5px;
}

.dogContainer, .catContainer, .foxContainer, .duckContainer{
  background: rgba(255, 0, 0, 0.9);
  width: 100%;
  height: 700px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.dogContainer{
  background-image: url('./components/images/pexels-chevanon-1108099.jpg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  background-blend-mode: normal;
}
#dogInfo, #catInfo, #foxInfo, #duckInfo{
  width: 800px;
  height: 400px;
  background: rgba(0, 0, 0, 0.774);
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  align-items: center;
  font-family: "Open Sans", sans-serif;
  font-optical-sizing: auto;
  font-weight: 300;
  font-style: normal;
  font-variation-settings:
    "wdth" 87.5;
    border-radius: 20px;
}

.dogText, .catText, .foxText, .duckText{
color: rgb(255, 255, 255);
font-size: 20px;
word-spacing: 5px;
margin: 7px;
padding: 5px;
font-weight: 500;
}

.catContainer{
  background-image: url('./components/images/pexels-didsss-1276553.jpg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
}
.foxContainer{
  background-image: url('./components/images/pexels-monicore-134058.jpg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
}
.duckContainer{
  background-image: url('./components/images/pexels-kristi-evans-383398-1024501.jpg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
}
.animal{
  color: white;
  text-align: center;
  font-size: 22px;
  text-transform: capitalize;
}
.btnContainer{
  width: 100%;
  display: flex;
  justify-content: end;
  margin-left: 20px;
}
.btn{
  border-radius: 5px;
  background: rgb(255, 0, 85);
  margin-right: 20px;
}
.btn a{
  text-decoration: none;
  font-size: 1.9em;
  margin-right: 10px;
  color: white;
  text-align: center;
  padding: 5px;
  text-transform: capitalize;
  cursor: pointer;
}
#cats, #dogs, #foxes, #ducks{
  transition: ease 3s;
}
.footer{
  background: rgb(255, 0, 85);
  height: 40px;
  align-content: center;

}
.footer div {
  width: 100%;
  display: flex;
  justify-content: center;
}
.footer div p{
  color: white;
  font-size: 20px;
}
.heart{
  color: rgb(0, 0, 0);
}
/*dogImages*/
.dogImageContainer{
  width: 100%;
  height: fit-content;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.dogImage{
  width: 300px;
  height: 350px;
  margin-bottom: 99px;
  margin-top: 5px;
}
.dogImage img{
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 10px;
}
.rating{
  width: 100%;
  height: 40px;
  background: rgb(255, 255, 255);
  border-radius: 7px;
}
.animalName{
  width: 100%;
  text-transform: capitalize;
  font-weight: 700;
  font-size: 20px;
  text-align: center;
}
.shadow{
  background: rgb(0, 0, 0);
  margin-top: 10px;
  box-shadow: 5px 5px 10px;
  border-radius: 10px;
  height: 410px;
  width: fit-content;
  margin-left: 10px;
}
.loading{
  width: 100%;
  height: 90px;
    font-size: 40px;
  text-transform: capitalize;
  font-weight: 400;
  text-align: center;
  color: rgb(0, 0, 0);
  align-items: center;
  display: flex;
  justify-content: center;
}
.loading span{
  opacity: 1;
  display: inline-block;
  border-radius: 5px;
  animation: bounce 1s infinite;
}
.loading span:nth-of-type(1){
  animation-delay: 0.1s;
}
.loading span:nth-of-type(2){
  animation-delay: 0.2s;
}.loading span:nth-of-type(3){
  animation-delay: 0.3s;
}.loading span:nth-of-type(4){
  animation-delay: 0.4s;
}.loading span:nth-of-type(5){
  animation-delay: 0.5s;
}.loading span:nth-of-type(6){
  animation-delay: 0.6s;
}.loading span:nth-of-type(7){
  animation-delay: 0.7s;
}.loading span:nth-of-type(8){
  animation-delay: 0.8s;
}.loading span:nth-of-type(9){
  animation-delay: 0.9s;
}.loading span:nth-of-type(10){
  animation-delay: 10s;
}
@keyframes bounce {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-20px);
  }
}
/*mobile designs*/

@media (max-width: 1000px) {
  /*dogImages*/
.dogImage{
  width: 220px;
  height: 250px;
  margin-bottom: 49px;
  margin-top: 5px;
}
.shadow{
  background: rgb(0, 0, 0);
  margin-top: 10px;
  height: fit-content;
  width: fit-content;
  margin-left: 5px;
}
.loading{
    font-size: 30px;
  font-weight: 300;
}
}
@media (max-width: 680px) {
  /*dogImages*/
.dogImage{
  width: 150px;
  height: 200px;
}
.loading{
    font-size: 25px;
}
}

@media (max-width: 500px) {
header{
  background: rgb(61, 58, 59);
}
/*home*/
.btn{
  background: rgb(0, 0, 0);
}
.btn a{
  font-size: 20px;
}
.footer{
  background: rgb(61, 58, 59);

}
.footer div p{
  font-size: 15px;
}
.heart{
  color: rgb(255, 0, 191);
}
.dogImage{
  width: 150px;
  height: 200px;
  margin-bottom: 0px;
  margin-top: 5px;
}
.dogImage img{
  width: 100%;
}
.rating{
  display: none;
}
.checkbtn{
  font-size: 1em;
}
}